/* ===== Base Styles ===== */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  color: #fff;
  line-height: 1.6;
  overflow-x: hidden;
}

.App {
  text-align: center;
  padding: 20px;
}

/* ===== Header ===== */
.header {
  padding: 40px 20px;
  animation: slideDown 1s ease-out;
}

.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid #00d4ff;
  margin-bottom: 15px;
  animation: float 3s ease-in-out infinite;
}

/* Floating animation for profile */
@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-12px); }
  100% { transform: translateY(0px); }
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #00d4ff;
}

p {
  max-width: 600px;
  margin: 0 auto;
}

/* ===== Section Titles ===== */
h2 {
  margin: 30px 0 20px;
  font-size: 2rem;
  position: relative;
  display: inline-block;
  color: #ff6f61;
  animation: fadeInUp 1s ease-in-out;
}

h2::after {
  content: "";
  display: block;
  height: 3px;
  width: 60%;
  margin: 6px auto 0;
  background: #ff6f61;
  border-radius: 10px;
}

/* ===== Skills ===== */
.skills ul {
  list-style: none;
  padding: 0;
}

.skills li {
  background: #0f3460;
  margin: 10px auto;
  padding: 12px 20px;
  width: 220px;
  border-radius: 25px;
  transition: transform 0.3s, background 0.3s;
  cursor: pointer;
}

.skills li:hover {
  transform: scale(1.1);
  background: #e94560;
}

/* ===== Projects ===== */
.project-card {
  background: #0f3460;
  margin: 20px auto;
  padding: 20px;
  border-radius: 15px;
  width: 80%;
  max-width: 500px;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.project-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 10px 25px rgba(0, 212, 255, 0.4);
}

.project-card h3 {
  color: #00d4ff;
}

.project-card a {
  color: #ff6f61;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.project-card a:hover {
  color: #fff;
}

/* ===== Contact ===== */
.contact form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
  margin: auto;
}

.contact input,
.contact textarea {
  padding: 12px;
  border-radius: 8px;
  border: none;
  outline: none;
  background: #1b1b2f;
  color: #fff;
  transition: box-shadow 0.3s, transform 0.3s;
}

.contact input:focus,
.contact textarea:focus {
  box-shadow: 0 0 10px #00d4ff;
  transform: scale(1.03);
}

.contact button {
  padding: 12px;
  border: none;
  border-radius: 8px;
  background: linear-gradient(135deg, #00d4ff, #ff6f61);
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  transition: transform 0.3s, background 0.3s;
}

.contact button:hover {
  transform: scale(1.1);
  background: linear-gradient(135deg, #ff6f61, #00d4ff);
}

/* ===== Footer ===== */
footer {
  margin-top: 40px;
  padding: 20px;
  background: #111;
  color: #aaa;
  font-size: 0.9rem;
}

/* ===== Animations ===== */
@keyframes slideDown {
  from { transform: translateY(-50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes fadeInUp {
  from { transform: translateY(40px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.fade-in {
  animation: fadeInUp 1s ease-in-out;
}
